.modal {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0,0,0,0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}
.modal-body {
    padding: 2rem;
    width: 400px;
    height: 200px;
    border-radius: 5px;
    background: white;
}