.Item_wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem 1rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-bottom: 0.5rem;
}
.input_item {
    margin-right: 1rem;
}
.button_item {
    background: red;
    border-radius: 50%;
    color: white;
    border: none;
}
